"use client";

import React, { useState, useEffect } from "react";

export default function Carousel() {
  const [banners, setBanners] = useState<Array<{id:number; image:string; link:string}>>([]);
  const [current, setCurrent] = useState(0);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchBanners() {
      try {
        const res = await fetch("/api/banners");
        if (!res.ok) throw new Error("Failed to fetch banners");
        const data = await res.json();
        setBanners(data);
      } catch (error) {
        console.error(error);
      } finally {
        setLoading(false);
      }
    }
    fetchBanners();
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrent((prev) => (prev === banners.length - 1 ? 0 : prev + 1));
    }, 5000); // Change slide every 5 seconds
    return () => clearInterval(interval);
  }, [banners]);

  const prevSlide = () => {
    setCurrent(current === 0 ? banners.length - 1 : current - 1);
  };

  const nextSlide = () => {
    setCurrent(current === banners.length - 1 ? 0 : current + 1);
  };

  if (loading) {
    return <div className="text-center py-8">Loading banners...</div>;
  }

  if (banners.length === 0) {
    return <div className="text-center py-8">No banners available</div>;
  }

  return (
    <div className="relative max-w-7xl mx-auto my-8">
      <div className="overflow-hidden rounded-lg">
        <a href={banners[current].link} aria-label="Banner Link">
          <img
            src={banners[current].image}
            alt={`Banner ${current + 1}`}
            className="w-full h-auto object-cover"
          />
        </a>
      </div>
      <button
        onClick={prevSlide}
        aria-label="Previous Slide"
        className="absolute top-1/2 left-4 transform -translate-y-1/2 bg-black bg-opacity-50 text-white rounded-full p-2 hover:bg-opacity-75 focus:outline-none"
      >
        &#10094;
      </button>
      <button
        onClick={nextSlide}
        aria-label="Next Slide"
        className="absolute top-1/2 right-4 transform -translate-y-1/2 bg-black bg-opacity-50 text-white rounded-full p-2 hover:bg-opacity-75 focus:outline-none"
      >
        &#10095;
      </button>
      <div className="flex justify-center mt-4 space-x-2">
        {banners.map((banner, index) => (
          <button
            key={banner.id}
            onClick={() => setCurrent(index)}
            aria-label={`Go to slide ${index + 1}`}
            className={`w-3 h-3 rounded-full ${
              index === current ? "bg-black" : "bg-gray-300"
            }`}
          />
        ))}
      </div>
    </div>
  );
}
