"use client";

import React, { useState, useEffect } from "react";

export default function CategorySection() {
  const [categories, setCategories] = useState<Array<{id:number; name:string; image:string; link:string}>>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchCategories() {
      try {
        const res = await fetch("/api/categories");
        if (!res.ok) throw new Error("Failed to fetch categories");
        const data = await res.json();
        setCategories(data);
      } catch (error) {
        console.error(error);
      } finally {
        setLoading(false);
      }
    }
    fetchCategories();
  }, []);

  if (loading) {
    return <div className="text-center py-8">Loading categories...</div>;
  }

  if (categories.length === 0) {
    return <div className="text-center py-8">No categories available</div>;
  }

  return (
    <section className="max-w-7xl mx-auto my-12 px-4 sm:px-6 lg:px-8">
      <h2 className="text-center text-2xl font-semibold mb-8 uppercase">Category</h2>
      <div className="flex justify-center space-x-12">
        {categories.map((category) => (
          <a
            key={category.id}
            href={category.link}
            className="flex flex-col items-center space-y-2 hover:opacity-80"
          >
            <img
              src={category.image}
              alt={category.name}
              className="w-24 h-24 object-contain rounded-md"
            />
            <span className="text-sm font-medium text-black">{category.name}</span>
          </a>
        ))}
      </div>
    </section>
  );
}
