"use client";

import React, { useState, useEffect } from "react";

function StarRating({ rating }: { rating: number }) {
  return (
    <div className="flex space-x-1 text-yellow-500">
      {[...Array(5)].map((_, i) => (
        <svg
          key={i}
          className={`w-5 h-5 fill-current ${i < rating ? "text-yellow-500" : "text-gray-300"}`}
          viewBox="0 0 20 20"
          fill="currentColor"
          aria-hidden="true"
        >
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.286 3.974a1 1 0 00.95.69h4.18c.969 0 1.371 1.24.588 1.81l-3.39 2.462a1 1 0 00-.364 1.118l1.287 3.974c.3.922-.755 1.688-1.54 1.118l-3.39-2.462a1 1 0 00-1.175 0l-3.39 2.462c-.784.57-1.838-.196-1.539-1.118l1.287-3.974a1 1 0 00-.364-1.118L2.045 9.4c-.783-.57-.38-1.81.588-1.81h4.18a1 1 0 00.95-.69l1.286-3.974z" />
        </svg>
      ))}
    </div>
  );
}

export default function CustomerFeedbackCarousel() {
  const [feedbacks, setFeedbacks] = useState([]);
  const [current, setCurrent] = useState(0);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchFeedbacks() {
      try {
        const res = await fetch("/api/feedback");
        if (!res.ok) throw new Error("Failed to fetch feedbacks");
        const data = await res.json();
        setFeedbacks(data);
      } catch (error) {
        console.error(error);
      } finally {
        setLoading(false);
      }
    }
    fetchFeedbacks();
  }, []);

  const prevFeedback = () => {
    setCurrent(current === 0 ? feedbacks.length - 1 : current - 1);
  };

  const nextFeedback = () => {
    setCurrent(current === feedbacks.length - 1 ? 0 : current + 1);
  };

  if (loading) {
    return <div className="text-center py-8">Loading feedback...</div>;
  }

  if (feedbacks.length === 0) {
    return <div className="text-center py-8">No feedback available</div>;
  }

  return (
    <section className="max-w-7xl mx-auto my-12 px-4 sm:px-6 lg:px-8">
      <h2 className="text-center text-2xl font-semibold mb-4 uppercase">Customers Feedback</h2>
      <div className="flex justify-center mb-4">
        <button
          onClick={prevFeedback}
          aria-label="Previous Feedback"
          className="bg-black text-white rounded-full p-2 mx-2 hover:bg-gray-800 focus:outline-none"
        >
          &#10094;
        </button>
        <button
          onClick={nextFeedback}
          aria-label="Next Feedback"
          className="bg-black text-white rounded-full p-2 mx-2 hover:bg-gray-800 focus:outline-none"
        >
          &#10095;
        </button>
      </div>
      <div className="bg-gray-50 p-6 rounded-md shadow-md max-w-3xl mx-auto">
        <p className="text-sm italic mb-4">"{feedbacks[current].comment}"</p>
        <div className="flex justify-between items-center">
          <div>
            <h3 className="font-semibold text-black">{feedbacks[current].name}</h3>
            <p className="text-xs text-gray-600">{feedbacks[current].date}</p>
          </div>
          <StarRating rating={feedbacks[current].rating} />
        </div>
      </div>
    </section>
  );
}
