"use client";

import React, { useState, useEffect } from "react";
import ProductCard from "./ProductCard";

export default function NewArrivals() {
  const [products, setProducts] = useState<Array<{
    id: number;
    name: string;
    image: string;
    price: number;
    originalPrice: number;
    discountPercent: number;
    link: string;
  }>>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchProducts() {
      try {
        const res = await fetch("/api/products");
        if (!res.ok) throw new Error("Failed to fetch products");
        const data = await res.json();
        setProducts(data);
      } catch (error) {
        console.error(error);
      } finally {
        setLoading(false);
      }
    }
    fetchProducts();
  }, []);

  if (loading) {
    return <div className="text-center py-8">Loading products...</div>;
  }

  if (products.length === 0) {
    return <div className="text-center py-8">No products available</div>;
  }

  return (
    <section className="max-w-7xl mx-auto my-12 px-4 sm:px-6 lg:px-8">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-semibold uppercase">New Arrivals</h2>
        <a
          href="/NEW-ARRIVALS/collection/rGSRfcB-"
          className="text-sm font-semibold underline hover:text-gray-700"
        >
          View All
        </a>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6">
        {products.map((product) => (
          <ProductCard
            key={product.id}
            image={product.image}
            name={product.name}
            price={product.price}
            originalPrice={product.originalPrice}
            discountPercent={product.discountPercent}
            link={product.link}
          />
        ))}
      </div>
    </section>
  );
}
